var bgNumShadows = 30;

function wrapper_splash_shadows_start() {
  var splash = document.getElementById("splash");
  if (!splash)
    return;

  for (var i = 0; i < bgNumShadows; i++) {
    var div = document.createElement("div");
    div.id = "bgShadow" + i;

    var width = 50 + i * Math.floor(150 / bgNumShadows);
    div.style.width = width + "px";
    div.style.height = Math.floor(width / 5) + "px";

    var colorAdd = Math.floor((200 - width) / 20) * 3 + 1;
    if (colorAdd == 10)
      colorAdd = 'A';
    if (colorAdd == 11)
      colorAdd = 'B';
    if (colorAdd == 12)
      colorAdd = 'C';
    if (colorAdd == 13)
      colorAdd = 'D';
    if (colorAdd == 14)
      colorAdd = 'E';
    if (colorAdd == 15)
      colorAdd = 'F';
    if (colorAdd > 15)
      colorAdd = 'E';

    div.className = 'bg-shadow';
    div.style.background = "#2" + colorAdd + "2" + colorAdd + "2" + colorAdd;
    div.style.borderRadius = Math.floor(width / 2) + "px";
    div.style.position = "absolute";
    div.style.zIndex = 1;
    
    if(body_width !== undefined && body_height !== undefined){
      div.style.left = (-100 + Math.floor(Math.random() * (body_width + 1))) + "px";
      div.style.top = (50 + Math.floor(Math.random() * (body_height + 1))) + "px";
    }else{
      // use local default values from start
      div.style.left = (-100 + Math.floor(Math.random() * (gameWidth + 1))) + "px";
      div.style.top = (50 + Math.floor(Math.random() * (gameHeight + 1))) + "px";
    }
    splash.insertBefore(div, splash.firstChild);
  }
  // jw: ask vlad why this was removed:
  setTimeout(wrapper_splash_shadows_move, 20);
}

function wrapper_splash_shadows_move() {
  if (isSplashHidden)
    return;

  for (var i = 0; i < bgNumShadows; i++) {
    var div = document.getElementById("bgShadow" + i);
    var left = parseInt(div.style.left);
    var width = parseInt(div.style.width);
    left += Math.floor((200 - width) / 15) + 1;

    // TODO: REVIEW
    if (left > body_width + 200)
      left = -200;
    div.style.left = left + "px";
  }
  setTimeout(wrapper_splash_shadows_move, 20);
}

function fitTitle() {
  var title = document.getElementById("ad_title");
  var fontSize = title.clientHeight;
  console.log('fontSize: ' + fontSize + '   scroll height: ' + title.scrollHeight + '  width: ' + title.scrollWidth + '   client height: ' + title.clientHeight + '  width: ' + title.clientWidth + '   offset height: ' + title.offsetHeight + '  width: ' + title.offsetWidth);
  while (title.scrollHeight > title.clientHeight && fontSize >= 8) {
    fontSize -= 1;
    var styleAttr = document.createAttribute("style");
    styleAttr.value = "font-size: " + fontSize + "px;line-height: " + fontSize + "px;padding-left:" + title_pad_left + "px;padding-right:" + title_pad_right + "px";
    title.setAttributeNode(styleAttr);
    console.log('fontSize: ' + fontSize + '   scroll height: ' + title.scrollHeight + '  width: ' + title.scrollWidth + '   client height: ' + title.clientHeight + '  width: ' + title.clientWidth + '   offset height: ' + title.offsetHeight + '  width: ' + title.offsetWidth);
  }
}

